* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    background-color: #f1f2f5;
}

.app__body{
    display: flex;
}
.header {
	padding: 15px 20px;
	display: flex;
	justify-content: space-between;
	position: -webkit-sticky;
	position: sticky;
	background-color: white;
	z-index: 100;
	top: 0;
	box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
	
}

.header__left{
	display: flex;
	justify-content: space-evenly;
}

.header__input{
	display: flex;
	align-items: center;
	background-color: #eff2f5;
	padding: 10px;
	margin-left: 10px;
	border-radius: 999px;
}

.header__input > input{
	border: none;
	background-color: transparent;
	outline-width: 0;
}

.header__left > img {
	height: 40px;

}

.header__center{
	display: flex;
	flex : 1 1;
	justify-content: center;

}

.header__option > .MuiSvgIcon-root{
	color: grey;
}

/* Hover over top middle bar icons turns them into Facebook Blue */
.header__option:hover > .MuiSvgIcon-root{
	color: #2e81f4;
}

/* Target the active container material UI icon, make the icon blue */
.header__option--active > .MuiSvgIcon-root{
	color: #2e81f4;
}

/* Target the active container, give it a blue bottom border */
.header__option--active{
	border-bottom: 4px solid #2e81f4;
}

.header__option{
	display: flex;
	align-items: center;
	padding: 0 30px;
	cursor: pointer;
}

/* Hover over top middle bar icons to show a background grey shadow */
.header__option:hover{
	background-color: #eff2f5;
	border-radius: 10px;
	align-items: center;
	padding: 0 30px;
	border-bottom: none;
}

/* Avatar + Profile Name part */
.header__info{
	display: flex;
	align-items: center;
}

.header__info > h4{
	margin-left: 10px;
}

.header__info > img{
	margin-left: 10px;
}

.header__info > img{
	filter: alpha(opacity=100);
    opacity: 1;
}

.header__info > img:hover{
	filter: alpha(opacity=85);
    opacity: 0.85;
}


.header__right{
	display: flex;
}


.sidebar{
    padding: 25px 10px;
    flex: 0.33 1;
}
.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

/* on hover, background color goes lightgray with border rad*/
.sidebarRow:hover{
        background-color: lightgray;
        border-radius: 10px;
}

.sidebarRow > h4{
    margin-left: 20px;
    font-weight: 600;
}

.sidebarRow > .MuiSvgIcon-root{
    font-size: xx-large;
    color: #2e81f4;
}
.feed{
    flex: 1 1;
    padding: 30px 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storyReel{
    display: flex;
}
.story{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
    border-radius: 10px;
    margin-right: 10px;
    transition: -webkit-transform 100ms ease-in;
    transition: transform 100ms ease-in;
    transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
    cursor: pointer;
}

.story:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}

.story__avatar{
    margin: 10px;
    border: 5px solid #2e81f4;
}

.story > h4{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;
}
.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    width: 100%;
}

.messageSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}

.messageSender__top > form{
    flex: 1 1;
    display: flex;
}

.messageSender__top > form > input{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.messageSender__top > form > button{
    display: none;
}

.messageSender__input{
    flex: 1 1;
}

.messageSender__bottom{
    display: flex;
    justify-content: space-evenly;
}

.messageSender__option{
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
}

.messageSender__option > h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender__option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}
.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.post__avatar{
    margin-right: 10px;
}

.post__topInfo > h3{
    font-size: medium;
}

.post__topInfo > p{
    font-size: small;
    color: gray;
}
.post__bottom{
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0%;
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 15px;
}

.post__image > img{
    width: 100%;
}

.post__options{
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}

.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1 1;
}

.post__option > p{
    margin-left: 10px;
}

.post__options :hover{
    background-color: #eff2f5;
    border-radius: 5px;
}
.login {
    display: grid;
    place-items: center;
    height: 100vh;
}

.login > button{
    width: 300px;
    background-color: #2e81f4;
    color: #eff2f5;
    font-weight: 600;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.login > button:hover {
    background-color: #303030;
    color: #2e81f4;
}

.login > div > img:first-child {
    object-fit: contain;
    height: 150px;
}

.login > div > img:last-child {
    object-fit: contain;
    height: 50px;
    padding-top: 25px;
}

.login > div{
    display: flex;
    flex-direction: column;
}
